.content-landing {
  background-color: rgb(199, 247, 247);
  padding: 1.5rem;
}

.landing-image {
  background-color: red;
  border: 1px solid green;
  height: 500px;
}

.login {
  display: flex;
  flex-direction: column;
}

.contain-btn {
  display: flex;
  padding: 5px;
}

.custom:nth-child(1) {
  background-color: rgb(243, 243, 243);
  margin-right: 5px;
}
.custom:nth-child(2) {
  background-color: rgb(238, 194, 211);
}
.custom:nth-child(2) a {
  color: rgb(39, 39, 39);
}
.custom a {
  font-family: sans-serif;
  font-weight: bolder;
  text-decoration: none;
  padding: 10px;
  height: inherit;
  color: rgb(40, 136, 160);
}
.custom:hover {
  transition: 0.4s;
  background-color: rgb(214, 189, 189);
}
.custom:nth-child(2):hover {
  background-color: rgb(235, 146, 180);
}

@media screen and (max-width: 340px) {
  .content-landing {
    min-width: 281px;
  }
  .landing-image {
    min-width: 281px;
  }
}
