/* css reset */
html,
body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Courier New", Courier, monospace, sans-serif;
}

.wrapper {
  margin: 60px 100px;
}
nav {
  display: flex;
}
nav h1 {
  margin-right: auto;
}
